<script>
import StatusIcon from '@/components/StatusIcon.vue';
import { get_app_info } from '@/scripts/apps/product';

export default {
    data() {
        return {
            app_id: '',
            data: {
                app_id: '',
                app_secret: '',
            }
        }
    },
    methods: {
        async refreshData() {
            const loading = this.$vs.loading({
                target: this.$refs.card,
            })
            const resp = await get_app_info(this.app_id);
            if (resp.code !== 1) {
                this.$swal({
                    icon: "error",
                    title: "错误",
                    text: resp.msg,
                    confirmButtonText: "返回"
                }).then(() => {
                    this.$router.back();
                })
            }

            this.data = resp.data;
            loading.close();
        },
        copy(text) {
            navigator.clipboard.writeText(text);
            this.$swal({
                icon: "success",
                title: "成功",
                text: "复制成功",
                confirmButtonText: "OK"
            })
        }
    },
    async mounted() {
        this.app_id = this.$route.params.app_id;
        if (!this.app_id) {
            this.$swal({
                icon: "error",
                title: "错误",
                text: "错误的跳转",
                confirmButtonText: "返回"
            }).then(() => {
                this.$router.back();
            })
        }
        this.refreshData();
    },
    components: {
        StatusIcon
    }
}
</script>

