import request from "@/scripts/utils/request";

export async function get_product_price(product_id) {
    try {
        const resp = await request({
            method: "post",
            url: "/product/" + product_id,
        });
        return resp.data;
    } catch (e) {
        return e.data;
    }
}

export async function buy_product(product_id, config) {
    try {
        const resp = await request({
            method: "post",
            url: "/product/buy/",
            data: { 
                product_id,
                auto_renew_type: config.auto_renew_type,
                auto_renew_status: config.auto_renew_status,
                config: config.config
            }
        });
        return resp.data;
    } catch (e) {
        return e.data;
    }
}

